<template>
  <div v-show="props.compType === 'dialogList'">
    <!-- <el-form-item label="">
      <el-input class="input" v-model="props"></el-input>
    </el-form-item> -->
    <el-form-item label="ID" >
      <el-tooltip class="item" effect="dark" content=",ID" placement="left">
        <el-input class="input" v-model="props.id" ></el-input>
      </el-tooltip>
    </el-form-item>
    <el-form-item label="" >
      <el-input class="input" v-model="props.label"></el-input>
    </el-form-item>
    <el-form-item label="" >
      <el-input-number v-model="props.span"  :min="1" :max="24"/>
    </el-form-item>
    <el-form-item label="">
      <el-input-number v-model="props.labelWidth"  :min="1" :max="200"></el-input-number>
    </el-form-item>
    <el-form-item label="">
      <el-switch v-model="props.showLabel"></el-switch>
    </el-form-item>
    <el-form-item label="">
      <el-switch v-model="props.required"></el-switch>
    </el-form-item>
    <el-form-item label="" >
      <el-switch v-model="props.disabled"></el-switch>
    </el-form-item>
    <el-form-item label="" >
      <el-input class="input" v-model="props.value"></el-input>
    </el-form-item>
    <el-form-item label="">
        <el-input v-model="props.action"></el-input>
    </el-form-item>
    <el-form-item label="">
        <el-input v-model="props.title"></el-input>
    </el-form-item>
    <el-form-item label="">
        <el-switch v-model="props.multi"></el-switch>
    </el-form-item>
    <el-form-item label="">
        <el-switch v-model="props.showIndex"></el-switch>
    </el-form-item>
    <el-form-item label="">
        <el-input v-model="props.dval"></el-input>
    </el-form-item>
    <el-form-item label="">
        <el-input v-model="props.dlabel"></el-input>
    </el-form-item>
    <el-divider></el-divider>
      <el-table
        :data="colOptions"
      >
        <el-table-column property="label"  label="" align="center" />
        <el-table-column property="property"  label=""  align="center" />
        <el-table-column property="width"  label=""  align="center" width="70" />
        <el-table-column label="">
          <template slot-scope="scope">
          <el-button
            @click.native.prevent="deleteRow(scope.$index)"
            type="text"
            size="small">
            
          </el-button>
        </template>
        </el-table-column>
      </el-table>
    <br/>
    <el-alert title="," v-show="alertShow" type="error" :closable="false"/>
    <br>
    <el-form-item label="" label-width="60px">
        <el-input v-model="dLabel" />
    </el-form-item>
    <el-form-item label="" label-width="60px">
        <el-input v-model="dProperty"/>
    </el-form-item>
    <el-form-item label="" label-width="60px">
        <el-input-number v-model="dWidth" />
    </el-form-item>
    <div style="margin-left: 20px;">
      <el-button
        style="padding-bottom: 0"
        icon="el-icon-circle-plus-outline"
        type="text"
        @click="addColItem"
      >
        
      </el-button>
    </div>
  </div>
</template>
<script>
import fancyText from '../../extend/fancyText.vue';
/**
 * text
 */
export default {
  components: { fancyText },
  name:"textConfig",
  props:{
    props:{}
  },
  data(){
    return {
      colOptions:[],
      dLabel:'',
      dProperty:'',
      dWidth:150,
      alertShow:false
    }
  },
  methods:{
    addColItem(){
      const obj = {};
      if(this.dLabel!==''&&this.dProperty!==''){
        this.alertShow = false;
        obj.label = this.dLabel;
        obj.property = this.dProperty;
        obj.width = this.dWidth;
        this.colOptions.push(obj);
        this.resetFields();
      }else{
        this.alertShow = true;
      }
    },
    resetFields(){
      this.dLabel = '';
      this.dProperty = '';
      this.dWidth = 150;
    },
    deleteRow(index){
      this.colOptions.splice(index,1);
    }
  },
  mounted() {
    this.$nextTick(()=> {
      this.colOptions = this.colOptions.concat(JSON.parse(this.props.colConf));
    })
  },
  watch:{
    'colOptions'(newVal){
      this.props.colConf = JSON.stringify(newVal);
    }
  }
}
</script>
